import React from "react"
import Layout from "../layouts/layout"
import SEO from "../components/seo"
import styled from "styled-components"

const About = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  margin: 20vh 10vw 10vw 10vh;
  justify-items: center;
  align-items: center;
  justify-content: center;
  font-family: Consolas, sans-serif;
  overflow: auto;
  
  p{
    text-align: center;
    justify-content: center;
  }
  
  div{
    display: flex;
    flex: 0 1 auto;
    align-items: center;
    justify-items: center;
    justify-content: center;
    text-align: center;
    vertical-align:center;
    horiz-align: center;
    height: 20vh;
    margin: 5vh 5vw auto;
  }
  
  
  @media(max-device-width: 480px){
    margin: 5vh 5vw 5vw 5vh;
    grid-template-columns: auto;
    grid-template-rows: auto;
    font-size: 1rem;
    font-weight: 100;
  }
`

const AboutPage = () => {
  return (
    <Layout>
      <SEO title="About"/>
      <About>
        <div>first</div>
        <div>second</div>
        <div>third</div>
        <div>fourth</div>
        <div>five</div>
        <div>six</div>
      </About>
    </Layout>
  )
}


export default AboutPage
